<template>

  <div class="container">    
    <div v-if="currentBlog" class="content">
      <h1 class="center">{{currentBlog.title}}</h1>      
      <p class="center">
        By Bibhuti Poudyal | {{currentBlog.day + " " + currentBlog.month + ", " + currentBlog.year}}
      </p>
      <br><br>
      <span class="" v-html="currentBlog.content"></span>      
      <br>
      <br>
      <hr>
      
      <div class="comments">
        <vue-disqus shortname="bibhuticoder" :identifier="currentBlog.id" :url="'http://localhost:8080/#/blogs/' + currentBlog.id"></vue-disqus>
      </div>    
       
    </div>
    <div v-else>
        <p class="lead">Loading...</p>
    </div>
  </div>
</template>

<script>
import VueDisqus from 'vue-disqus/VueDisqus.vue'
export default {
  components: {VueDisqus},
  name: 'blog',
  data(){
    return{
      searchKeyword: ''
    }
  },
  methods: {
    
  },
  computed:{
    currentBlog(){      
      return this.$store.state.currentBlog;
    },
  },

  created(){
    var blogId = this.$route.params.id; 
    this.$store.dispatch('getCurrentBlog', {
      id: blogId,
      callback:function () {   
      }
    });

    


  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  
 .content{
    width: 75%;
    margin: 0 auto;
    font-family: Segoe UI !important;    
    font-weight: 400 !important;
    font-style: normal !important;
    font-size: 15px !important;
    line-height: 1.58 !important;
    letter-spacing: -.003em !important;
 }

 .center{
   text-align: center;
 }


</style>



